.page-services {
  background-color:#1B2D37;
}
.page-services__wrapper {
  display: flex;
  flex-direction: column;
  margin:0 auto;
  width: 62%;
  box-sizing: border-box;
}
.page-services__wrapper h2 {
  font-family: 'Roboto Condensed', 'Arial', sans-serif;
  font-size: 36px;
  line-height: 50px;
  font-weight: 400;
  color:#ffffff;
  margin:0;
  margin-top: 91px;
}
.services-list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  list-style: none;
  padding:0;
  margin: 0;
  margin-bottom: 127px;
}
.services-item {
  min-height: 620px;
  width: 31%;
}
.services-item__link {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
  text-decoration: none;
}
.services-item__link__name {
  font-family: 'Roboto Condensed', 'Arial', sans-serif;
  font-size: 20px;
  line-height: 30px;
  font-weight: 400;
  color:#ffffff;
  width: 40%;
  margin-left: 43px;
  margin-top: 53px;
}
.services-item:first-child {
  background: url(/img/producing-desktop@1x.png) no-repeat;
  background-size: cover;
}
.services-item:nth-child(2n) {
  background:url(/img/installing-desktop@1x.png) no-repeat;
  background-size: cover;
}
.services-item:nth-child(3n) {
  background:url(/img/led-desktop@1x.png) no-repeat;
  background-size: cover;
}


.services-item__link__arrow {
  position: relative;
  width: 63px;
  height: 63px;
  background-color: #00B2FF;
  margin:0;
  align-self: flex-end;
}
.services-item__link__arrow::before {
  content: '';
  position: absolute;
  width: 9px;
  height: 8px;
  border-top: 1px solid #ffffff;
  border-right: 1px solid #ffffff;
  margin-right: 60px;
  transform: rotate(45deg);
  top: 25px;
  left:24px;
}
@media(max-width:1023px) {
  .page-services__wrapper {
    width: 93.5%;
  }
  .page-services__wrapper h2 {
    font-size: 32px;
    line-height: 45px;
    margin-top: 53px;
    margin-bottom: 46px;
  }
  .services-list {
    flex-direction: column;
    width: 100%;
    margin-bottom: 78px;
  }
  .services-item {
    min-height: 256px;
    width: 100%;
    margin-bottom: 36px;
  }
  .services-item:last-child{
    margin-bottom: 0;
  }
  .services-item__link {
    min-height: 256px;
    width: 100%;
  }
  .services-item:first-child {
    background: url(/img/producing-tablet@1x.png) no-repeat;
    background-size: cover;
  }
  .services-item:nth-child(2n) {
    background:url(/img/installing-tablet@1x.png) no-repeat;
    background-size: cover;
  }
  .services-item:nth-child(3n) {
    background:url(/img/led-tablet@1x.png) no-repeat;
    background-size: cover;
  }
  .services-item__link__name {
    width: 24%;
    margin-left: 43px;
    margin-top: 43px;
  }
}
@media(max-width:767px) {
  .page-services__wrapper {
    width: 90.6%;
  }
  .page-services__wrapper h2 {
    font-size: 28px;
    line-height: 39px;
    margin-top: 47px;
    margin-bottom: 46px;
    margin-left: 5px;
  }
  .services-list {
    margin-bottom: 64px;
  }
  .services-item {
    min-height: 256px;
    width: 100%;
    margin-bottom: 30px;
  }
  .services-item__link__name {
    margin-top: 17px;
    margin-left: 32px;
    width: 55%;
  }
  .services-item:first-child {
    background: url(/img/producing-mobile@1x.png) no-repeat;
    background-size: cover;
  }
  .services-item:nth-child(2n) {
    background:url(/img/installing-mobile@1x.png) no-repeat;
    background-size: cover;
  }
  .services-item:nth-child(3n) {
    background:url(/img/led-mobile@1x.png) no-repeat;
    background-size: cover;
  }
}
