.page-about {
  padding-bottom: 110px;
  background: url(/img/geometry-desktop@1x.png) no-repeat;
  background-size: cover;
}
.page-about__wrapper {
    display: flex;
    flex-direction: column;
    margin:0 auto;
    width: 61%;
    box-sizing: border-box;
    background:url(/img/2005-desktop@1x.png) no-repeat;
    background-size:contain;
    background-position: -28px 0px;
}
.page-about h2 {
    font-family: 'Roboto Condensed', 'Arial', sans-serif;
    font-size: 36px;
    line-height: 50px;
    font-weight: 400;
    color:#666666;
    margin:0;
    margin-bottom: 18px;
    margin-left: 180px;
}
.page-about p {
    font-family: 'PT Mono', 'Arial', sans-serif;
    font-size: 16px;
    line-height: 21px;
    font-weight: 400;
    color:#666666;
    margin:0;
    max-width: 87%;
    margin-left: 180px;
    height: inherit;
}
.page-about__description--tablet {
  display: none;
}
.page-about p:first-of-type{
    margin-bottom: 25px;
}
.page-about__more {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-family: 'Roboto Condensed', 'Arial', sans-serif;
    font-size: 14px;
    line-height: 16px;
    font-weight: 700;
    text-transform: uppercase;
    text-decoration: none;
    color:#00B2FF;
    align-self: flex-start;
    width: 180px;
    height: 45px;
    border:1px solid #00B2FF;
    box-sizing: border-box;
    margin-top: 30px;
    border-radius: 5px;
    margin-left: 177px;
}
@media(max-width:1023px) {
  .page-about {
    padding-bottom: 83px;
  }
  .page-about__wrapper {
    background: url(/img/2005-tablet@1x.png) no-repeat;
    width: 93.5%;
    background-position: -28px -8px;
  }
  .page-about h2 {
    font-size: 32px;
    line-height: 45px;
    margin-bottom: 20px;
  }
  .page-about__description--desktop {
    display: none;
  }
  .page-about__description--tablet {
    display: block;
  }
  .page-about p:first-of-type{
    margin-bottom: 30px;
}
  .page-about__more {
    margin-top: 36px;
  }
}
@media(max-width:767px) {
  .page-about {
    padding-top: 10px;
    padding-bottom: 65px;
  }
  .page-about__wrapper {
    background:none;
    width: 90.6%;
  }
  .page-about h2 {
    font-size: 28px;
    line-height: 39px;
    width: 100%;
    margin:0;
    margin-bottom: 22px;
  }
  .page-about p {
    margin:0;
    min-width: 100%;
  }
  .page-about .page-about__description--tablet {
    margin-top: 17px;
  }
  .page-about__more {
    margin-left: 0;
  }
}
